












import Vue from 'vue';
import axios from 'axios';

export default Vue.extend({
  name: 'Error',
  methods: {
    home() {
      this.$router.replace('/');
    },
    backward() {
      this.$router.back();
    },
    issue() {
      window.location.href = 'https://github.com/nju-lug/NJU-Mirror-Issue';
    }
  },
  mounted() {
    axios.get(this.$route.path).then(
      () => undefined,
      err => this.$message.error(err),
    );
  },
});
